.form {
  margin-bottom: 20px;
  &__input {
    padding: 5px;
    display: block;
    height: 30px;
    min-width: 250px;
    font-size: 16px;
    margin-bottom: 20px;
    border: 1px solid rgb(189, 186, 186);
    border-radius: 4px;
    color: #34404b;
    &::placeholder {
      color: rgb(189, 186, 186);
    }
  }
  &__button {
    padding: 5px 10px;
    color: #fff;
    font-size: 15px;
    font-weight: 400;
    border: none;
    background-color: #6d7072;
    border-radius: 5px;
    transition: all 0.1s linear;
    &:hover {
      background-color: #ff5912;
    }
  }
}
