.register-page {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;

  background-color: #f4f6f8;
  text-align: center;
  &__inner {
    padding: 40px;
    border-radius: 5px;
    background-color: #fff;
    box-shadow: 0 1rem 3rem rgba(0, 0, 0, 0.175) !important;
    margin-bottom: 300px;
  }
  &__title {
    font-size: 22px;
    font-weight: 500;
    margin-bottom: 20px;
    color: #34404b;
  }
  &__paragraph {
    font-size: 22px;
    font-weight: 500;
    margin-bottom: 20px;
    color: #34404b;
    span {
      display: block;
    }
    a {
      color: #ff5912;
      font-size: 15px;
      font-weight: 600;
    }
  }
}
