.goods {
  grid-area: content;
  text-align: center;
  display: flex;
  flex-wrap: wrap;
  gap: 30px;

  &__img-cont {
    margin: 0 auto 30px;
    position: relative;
    width: 110px;
    height: 140px;
  }
  &__img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
  &__item {
    position: relative;
    flex: 1 1 33%;
    max-width: 270px;
    height: 352px;
    background-color: #ffffff;
    flex-basis: 30%;
    min-width: 220px;
    padding: 30px 0;
    text-align: center;
    transition: all 0.1s linear;
    &:hover {
      box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
    }
  }
  &__title {
    font-family: 'Harmonia Sans Pro Cyr';
    padding: 0 5px;
    text-align: center;
    font-size: 18.1px;
    font-weight: 700;
    color: #34404b;
    height: 70px;
    overflow: hidden;
  }
  &__prise {
    display: inline-block;
    bottom: 50px;
    font-family: 'Harmonia Sans Pro Cyr';
    font-size: 18.1px;
    font-weight: 700;
    color: #ff5912;
    margin-bottom: 13px;
  }
  &__link {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  &__svg-cont {
    width: 50px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
  }
  &__heart-svg,
  &__cart-svg {
    cursor: pointer;
    z-index: 11;
    height: 22px;
    width: 22px;
    color: #b3b3b6;
    &:hover {
      transition: all 0.1s linear;
      transform: scale(1.2);
      color: #34404b;
    }
  }
  &__heart-svg--red {
    color: #ff5912;
    &:hover {
      color: #ff5912;
    }
  }
  &__svg-link {
    z-index: 10;
  }
}
